@keyframes spinner {
	0% {
		rotate: 0deg;
	}

	100% {
		rotate: 360deg;
	}
}

.loading-spinner {
	width: 5rem;
	height: 5rem;
	border: 1rem solid var(--color-secondary);
	border-top: 1rem solid var(--color-primary);
	border-radius: 50%;
	animation: spinner 1.5s linear infinite;
}
