@import '/styles/responsive';

.info {
	display: grid;
	grid-template-rows: 10% 90%;
	border: var(--border-size-info) solid var(--color-primary);
	border-radius: 1rem;
	width: 100%;
	height: 100%;
	font-size: 1.5rem;
	text-transform: capitalize;

	@include respond($bp-medium) {
		grid-template-rows: min-content 1fr;
		line-height: 1.25rem;
	}

	&__header {
		display: flex;
		align-items: center;
		justify-content: center;

		border-bottom: var(--border-size-info) solid var(--color-primary);
		width: 100%;
		height: 100%;
		font-size: 2rem;
		font-weight: bold;

		@include respond($bp-medium) {
			font-size: 1.5rem;
			border-bottom: none;
		}

		&-text {
			color: inherit;
		}
	}

	&__body {
		display: grid;
		grid-template-columns: 1fr 1fr;
		grid-template-rows: repeat(5, 1fr);

		@include respond($bp-medium) {
			grid-template-columns: repeat(4, 1fr);
			grid-template-rows: 1fr 1fr;
		}

		& > * {
			display: grid;
			grid-template-rows: 1fr 2fr;
			border-bottom: var(--border-size-info) solid var(--color-primary);

			@include respond($bp-medium) {
				border-top: var(--border-size-info) solid var(--color-primary);
				border-bottom: none;
			}
		}

		&-name {
			border-right: var(--border-size-info) solid var(--color-primary);

			@include respond($bp-medium) {
				grid-column: 1;
				grid-row: 1;
			}
		}

		&-dex {
			@include respond($bp-medium) {
				grid-column: 2;
				grid-row: 1;
				border-right: var(--border-size-info) solid var(--color-primary);
			}
		}

		&-type {
			grid-column: 1 / -1;
			border-right: none;

			@include respond($bp-medium) {
				grid-column: 3 / -1;
				grid-row: 1;
			}
		}

		&-abilities {
			grid-column: 1 / -1;

			@include respond($bp-medium) {
				grid-column: 3 / -1;
				grid-row: 2;
			}
		}

		&-height {
			border-right: var(--border-size-info) solid var(--color-primary);
		}

		&-weight {
			@include respond($bp-medium) {
				border-right: var(--border-size-info) solid var(--color-primary);
			}
		}

		&-identity {
			display: grid;
			grid-column: 1 / -1;
			grid-template-columns: 1fr 1fr 1fr;
			border-bottom: none;

			@include respond($bp-medium) {
				grid-template-rows: repeat(3, 1fr);
				grid-template-columns: 1fr;
				grid-column: -1;
				grid-row: 1 / -1;
			}

			@include respond($bp-small-3) {
				font-size: 1rem;
			}

			@include respond($bp-small-4) {
				font-size: 0.2rem;
			}

			& > * {
				display: grid;
				grid-row: 1 / -1;
				border-right: var(--border-size-info) solid var(--color-primary);

				@include respond($bp-medium) {
					grid-row: initial;
					border-right: none;
					border-left: var(--border-size-info) solid var(--color-primary);
					border-bottom: var(--border-size-info) solid var(--color-primary);
				}
			}

			&-last {
				border-right: none;
				border-bottom: none;
			}
		}

		&-top {
			align-self: center;
			justify-self: center;

			font-weight: bold;
			margin: 0.2rem;

			@include respond($bp-small-3) {
				font-size: 1.2rem;
			}

			@include respond($bp-small-4) {
				font-size: 1rem;
			}

			@include respond($bp-small-4) {
				font-size: 0.75rem;
			}
		}

		&-bot {
			background-color: var(--table-color-entry);
			border-radius: 1rem;
			margin: 0.3rem 0.3rem;
			display: flex;
			align-items: center;
			justify-content: space-around;
			font-size: 1.75rem;

			@include respond($bp-large) {
				font-size: 1.55rem;
			}

			@include respond($bp-medium) {
				font-size: 1.7rem;
			}

			@include respond($bp-small-2) {
				font-size: 1.2rem;
			}

			@include respond($bp-small-4) {
				font-size: 1rem;
			}

			&-checkbox {
				background-color: var(--color-white);
				margin-bottom: 1.5rem;

				@include respond($bp-medium) {
					margin-bottom: 0;
					padding-bottom: 0.5rem;
				}
			}

			&-types {
				width: 9rem;

				@include respond($bp-small-4) {
					width: 7.5rem;
				}

				@include respond($bp-small-6) {
					width: 6.75rem;
				}

				@include respond($bp-small-7) {
					width: 6rem;
				}
			}

			&-abilities {
				display: flex;
				flex-direction: column;

				&-hidden {
					font-size: 1.1rem;
					line-height: 0.75rem;
					font-style: italic;
					align-self: center;

					@include respond($bp-medium) {
						padding-top: 0.35rem;
					}
				}
			}

			// hw for height and weight
			&-hw {
				width: 100%;
				height: 100%;
				display: flex;
				align-items: center;
				justify-content: space-around;

				@include respond($bp-medium) {
					flex-direction: column;
				}

				&-text {
					text-transform: lowercase;
				}
			}
		}
	}
}
